:global {
    body .ant-input {
        font-family: "ITCAvantGardeProReg";
        font-weight: 900;
        font-size: 20px;
        height: 45px;
        border-radius: 20px;
    }
}

.sigin {
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #cacacad4;
    position: relative;

    & span {
        display: inline-block;
        padding: 10px;
        background: @colorOne;
        position: absolute;
        top: -20px;
        cursor: pointer;
    }
}

.Login {
    width: 100%;
    height: 100%;
    padding: 50px 20px 20px 20px;
    border-radius: 6px;
    background: @colorOne;
    background-size: cover;
    box-shadow: @boxShadowOne @colorTwo;
    position: relative;

    .logo {
        width:  100px;
        height: 100px;
        border-radius: 50px;
        position: absolute;
        top: -50px;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
        background: #ddd;
        background: @logoImg;
        border: 2px solid #fff;
        background-size: 100%;
        transition: all .2s linear;

        &:hover {
            top: -60px;
            width: 110px;
            height: 110px;
            border-radius: 55px;
            box-shadow: 0 4px 10px 2px #52525245;
            transition: all .2s linear;
        }
    }
    .form {
        padding: 20px 20px;

        .thirdparty {
            width: 90%;
            margin: auto;
            margin-top: 50px;
            // border-top: 2px solid #cacaca;
            padding-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-evenly;

            & i {
                font-size: 24px;
                cursor: pointer;
            }
        }

        .login-form-forgot {
            float: right;
        }

        .loginformbutton {
            font-family: "ITCAvantGardeProReg";
            font-weight: 900;
            letter-spacing: 4px;
            width: 100%;
            height: 42px;
            font-size: 16px;
            border-radius: 30px;
            &:hover {
                box-shadow: 0 4px 10px 2px #505050;
            }
        }
    }
}

@primary-color: #052763;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;